* {
    box-sizing: content-box;
}

.wrapper {
    min-height: 100vh;
    background-color: #a2ea50;
    display: flex;
    justify-content: center;
    align-items: center;
}
.title{
    display: flex;
    justify-content: center;
}
.title h2 {
    margin-top: 0;
}

.skillsContainer {
    border: 2px solid;
    min-height: 80vh;
    display: flex;
    flex-direction: column;


}

@media (min-width: 480px) {
    .skillsContainer {
        width: 80%;
        /*height: 100%;*/
        margin: 0 auto;
    }
}

.skillsList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
